@import url(https://fonts.googleapis.com/css?family=Pacifico|Quicksand:300,500);
body {
  position: relative;
  min-height: 90vh;
  margin: 0;
  padding: 0;
  font-family: "Quicksand", "Helvetica Neue", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: calc(0.8vw + 1vmin);
  background-color: #282c34;
  color: #F1F1F1; }

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New", monospace; }

a {
  color: #5A975E; }

h1 {
  margin: 10vh auto;
  font-size: calc(6vw + 2vmin);
  font-weight: 300;
  letter-spacing: 0.5vw;
  width: -webkit-fit-content;
  width: -moz-fit-content;
  width: fit-content;
  border-bottom: 1px solid currentColor; }

h2 {
  font-size: calc(3vw + 1vmin);
  text-align: center;
  text-transform: uppercase;
  font-weight: 300; }

h3 {
  font-size: calc(1.5vw + 1vmin);
  text-decoration: underline;
  letter-spacing: 0.1vw;
  text-align: center; }

.header-padding {
  margin-top: 7vh; }

.menu-buttons {
  -moz-user-select: none;
  -webkit-user-select: none;
  -ms-user-select: none;
  user-select: none;
  text-align: center;
  font-size: calc(1vw + 1vmin); }
  .menu-buttons span {
    margin: 0 5vw;
    padding: 1vh 1vw;
    border: 1px solid #F1F1F1;
    border-radius: 3px; }
  .menu-buttons span:hover {
    background-color: #F1F1F1;
    color: #282c34; }

.active-button {
  background-color: #F1F1F1;
  color: #282c34; }

.activeLink {
  font-weight: bold; }

header {
  background-color: #F1F1F1;
  height: 5vh;
  display: -webkit-flex;
  display: flex;
  -webkit-flex-direction: row;
          flex-direction: row;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: flex-end;
          justify-content: flex-end;
  font-size: calc(0.75vw + 2vmin);
  color: #282c34;
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw; }
  header a {
    color: #282c34;
    text-decoration: none;
    margin: 0 6.5vw; }
  header img, header span {
    width: 1.1em;
    position: absolute;
    top: 1vh;
    left: 1vw; }
  header span {
    font-family: 'Pacifico', cursive;
    font-size: 0.75em;
    white-space: nowrap;
    margin-left: 1.8em; }

footer {
  width: 100vw;
  background-color: black;
  position: absolute;
  bottom: 0;
  left: 0;
  padding: 1vh 0;
  font-size: calc(0.5vw + 1vmin);
  text-align: center; }
  footer span {
    margin: 0 10vw;
    text-transform: uppercase; }
    footer span a {
      text-decoration: none; }

#home div {
  margin: 10vh 10vw 0 10vw;
  display: -webkit-flex;
  display: flex; }
  #home div img {
    width: 40%;
    height: 100%;
    margin: 0;
    padding-bottom: 10vh; }
  #home div ul {
    padding-left: 5vw;
    margin-top: 0; }
    #home div ul li {
      margin-bottom: 1vh; }

#about div {
  margin: 0 5vw;
  padding-bottom: 5vh; }
  #about div p {
    text-align: center;
    margin: 7.5vh 0; }
  #about div .about-cards {
    margin: 15vh 0 10vh 0; }
    #about div .about-cards p {
      text-align: left;
      text-indent: 3vw;
      margin: 0; }
    #about div .about-cards img {
      width: 50vw;
      float: right;
      margin: 1vh 1vw; }

#portfolio div {
  margin: 0 5vw;
  padding-bottom: 5vh; }
  #portfolio div p {
    text-align: center;
    margin: 7.5vh 0; }
  #portfolio div .portfolio-cards img {
    width: 100%;
    margin: 10vh 0 5vh 0; }
  #portfolio div .portfolio-cards div {
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: row;
            flex-direction: row; }
    #portfolio div .portfolio-cards div ul {
      width: 50%;
      margin-right: 5vw; }
      #portfolio div .portfolio-cards div ul li {
        margin: 1vh 0; }
    #portfolio div .portfolio-cards div p {
      text-align: left;
      margin: 0 0 5vw 0;
      width: 50%; }

#skills div {
  margin: 0 5vw;
  padding-bottom: 5vh;
  display: -webkit-flex;
  display: flex; }
  #skills div p {
    text-align: center;
    margin: 7.5vh 0; }
  #skills div img {
    width: 40%;
    height: 100%;
    border: 5px dashed white; }
  #skills div ul {
    margin-left: 5vw; }
    #skills div ul li {
      margin-bottom: 1vh; }

#contact div {
  margin: 0 5vw;
  padding-bottom: 5vh;
  display: -webkit-flex;
  display: flex; }
  #contact div p {
    text-align: center;
    margin: 7.5vh 0; }
  #contact div img {
    width: 25%;
    height: 100%;
    padding-bottom: 5vh; }
  #contact div div {
    display: block; }
    #contact div div a {
      margin: 0 1vw; }

